body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
    background-color: #282c34;
    min-height: 100vh;
    color: #fff;
}

.come-back {
    font-size: 4em;
    margin-top: 2em;
    text-align: center;
}
.container {
    background-color: #282c34;
    height: 100vh;
    color: #fff;
    padding: 20px;
}

.date-header {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.date-extra {
    display: flex;
    flex-direction: column;
    text-align: left;
    font-size: 1.3em;
    border-left: 1px solid #999;
    padding-left: 0.5em;
    margin-left: 0.4em;
    width: 5em;
}

hr.divider {
    border: 0;
    border-top: 1px solid #999;
    /* margin: 10px -40px; */
}

.date-main {
    font-size: 6em;
}

.word {
    padding-bottom: .4em;
}

.word>header {
    font-size: 2.2em;
}

.word-type {
    font-size: 1.4em;
    font-style: oblique;
    font-weight: lighter;
    /* margin-top: -0.1em; */
}

.word-def {
    margin-top: .4em;
}

.word-usage {
    font-style: oblique;
    font-weight: lighter;
    margin-top: 1em;
}

.usage-header {
    font-size: 1.1em;
    color: #aaa;
    margin-bottom: .2em;
}
